document.addEventListener("DOMContentLoaded", () => {
	// Includes
	//? // Debounce
class Debounce {
	constructor(time = 100, functions = () => { }) {
		this.time = time;
		this.functions = functions;
		this.timeOut = null;
	}

	init() {
		clearTimeout(this.timeOut);
		this.timeOut = setTimeout(() => {
			this.functions();
		}, this.time);
	}
}
;
	//? // Burger

/* Бургер-меню
	1. Добавить модификаторы для бургера и меню.
	2. Изменить aria-expended.
	3. Запретить скролл.
	4. Выключать кнопку открытия меню до завершения анимации.
*/

class Burger {
	constructor(btn, list) {
		this.btn = btn;
		this.list = list;
		this.opened = false;
		this.isIphone = isiPhone || isiPad || isiPod;
		this.btnDisabled = false;
	}

	open() {
		this.disableBtn();
		this.btn.classList.add('burger--active');
		this.btn.setAttribute('aria-expanded', true);
		this.list.classList.add('header__nav--active');
		this.list.setAttribute('aria-hidden', false);
		if (this.isIphone) {
			setTimeout(() => {
				disableScroll();
			}, 300);
		} else {
			body.classList.add('lock')
		}
	}

	close() {
		this.disableBtn();
		if (this.isIphone) {
			enableScroll();
		} else {
			body.classList.remove('lock')
		}
		this.btn.classList.remove('burger--active');
		this.btn.setAttribute('aria-expanded', false);
		this.list.classList.remove('header__nav--active');
		this.list.setAttribute('aria-hidden', true);
	}

	toggle() {
		if (this.btn.classList.contains('burger--active')) {
			this.close();
		} else {
			this.open();
		}
	}

	disableBtn() {
		this.btn.disabled = true;
		this.list.addEventListener('transitionend', () => {
			this.btn.disabled = false;
		})
	}
};
	//? // Swiper
class MobileSwiper {
	constructor(selector, swiperObj, breakpoint) {
		this.selector = document.querySelector(`.${selector}`);
		this.selectorClassName = selector;
		this.breakpoint = breakpoint;
		this.swiperObj = swiperObj;
		this.initSlider = null;
	}
	create() {
		if (window.innerWidth <= this.breakpoint && this.selector.dataset.mobile == 'false') {
			this.initSlider = new Swiper(this.selector, this.swiperObj);
			this.selector.dataset.mobile = 'true';
		}
	}
	destroy() {
		if (window.innerWidth > this.breakpoint) {
			this.selector.dataset.mobile = 'false';
			if (this.selector.classList.contains(`${this.selectorClassName}initialized`)) {
				this.initSlider.destroy();
			}
		}
	}
	init() {
		this.selector.dataset.mobile = 'false';
		this.create();
		this.destroy();
	}
}
;
	// // Variables
const isMobileDevice = (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent));
const isSmoothScrollSupported = 'scrollBehavior' in document.documentElement.style;
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);
const media = window.matchMedia("(max-width: 992px)");

const body = document.querySelector('.body');
const header = document.querySelector('.header');
const headerLinks = header.querySelectorAll('.header__link');
const burgerBtn = header.querySelector('.burger');
const burgerList = header.querySelector('.header__nav');
const burger = new Burger(burgerBtn, burgerList);

const classificationSwiperObj = {
	containerModifierClass: "classification-swiper",
	wrapperClass: "classification-swiper__wrapper",
	slideClass: "classification-swiper__slide",
	slidesPerView: 1,
	grabCursor: true,
	pagination: {
		el: ".classification-swiper__pagination",
		clickable: true
	},
	a11y: {
		paginationBulletMessage: 'Перейти к слайду номер {{index}}'
	},
	watchSlidesVisibility: true
};
const classificationSwiper = new MobileSwiper('classification-swiper', classificationSwiperObj, 767);

const dietSwiperObj = {
	containerModifierClass: "diet-swiper",
	wrapperClass: "diet-swiper__wrapper",
	slideClass: "diet-swiper__slide",
	slidesPerView: 1.25,
	spaceBetween: 25,
	grabCursor: true,
	pagination: {
		el: ".diet-swiper__pagination",
		clickable: true
	},
	a11y: {
		paginationBulletMessage: 'Перейти к слайду номер {{index}}'
	},
	watchSlidesVisibility: true
};
const dietSwiper = new MobileSwiper('diet-swiper', dietSwiperObj, 767);

const resizeFunc = () => {
	classificationSwiper.init();
	dietSwiper.init();
	console.log('ResizeDebounce');
}
const resizeDebounce = new Debounce(300, resizeFunc);

const scrollFunc = () => {
	fixHeader();
	console.log('ScrollDebounce');
}
const scrollDebounce = new Debounce(100, scrollFunc);


;
	// // Fixed Header
function fixHeader() {
	let pos = window.pageYOffset;
	if (!header.classList.contains('header--active') && pos > window.innerHeight) {
		header.classList.add('header--active');
		burgerBtn.classList.add('burger--scroll');
		if ('animate' in header) {
			header.animate([
				{ top: '-100px' },
				{ top: '0' }
			], {
				duration: 200,
				easing: 'ease-out'
			});
		}
	} else if (header.classList.contains('header--active') && window.innerHeight > pos && !burgerBtn.classList.contains('burger--active')) {
		if ('animate' in header) {
			let anim = header.animate([
				{ top: '0' },
				{ top: '-100px' }
			], {
				duration: 200,
				easing: 'ease-out'
			});

			anim.addEventListener('finish', () => {
				header.classList.remove('header--active');
				burgerBtn.classList.remove('burger--scroll');
			})
		}
	}
}

// Scroll To Target
function scrollToTarget(id) {
	const target = document.querySelector(id);
	if (isiPad || isiPhone || isiPod && target !== null) {
		const bodyRect = document.body.getBoundingClientRect();
		const elemRect = target.getBoundingClientRect();
		const offset = elemRect.top - bodyRect.top;

		const requestAnimFrame = (function () {
			return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || function (callback) {
				window.setTimeout(callback, 1000 / 60);
			};
		})();
		Math.easeInOutQuad = function (t, b, c, d) {
			t /= d / 2;
			if (t < 1) {
				return c / 2 * t * t + b
			}
			t--;
			return -c / 2 * (t * (t - 2) - 1) + b;
		};
		function scrollToIos(to, callback, duration) {
			function move(amount) {
				document.documentElement.scrollTop = amount;
				document.body.parentNode.scrollTop = amount;
				document.body.scrollTop = amount;
			}

			function position() {
				return document.documentElement.scrollTop || document.body.parentNode.scrollTop || document.body.scrollTop;
			}
			let start = position(),
				change = to - start,
				currentTime = 0,
				increment = 20;
			duration = (typeof (duration) === 'undefined') ? 500 : duration;

			let animateScroll = function () {
				currentTime += increment;
				let val = Math.easeInOutQuad(currentTime, start, change, duration);
				move(val);
				if (currentTime < duration) {
					requestAnimFrame(animateScroll);
				} else {
					if (callback && typeof (callback) === 'function') {
						callback();
					}
				}
			};
			animateScroll();
		}
		scrollToIos(offset - 15, null, 200);
		return;
	}

	if (target !== null) {
		let pos = target.offsetTop - (media.matches ? 20 : -1);
		window.scrollTo({
			top: pos,
			behavior: 'smooth'
		});
	}
}

;
	// // scrollLockIOS

// Выключение скролла
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

// Включение скролла
let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
};
	// // Animate On Scroll
// class="animate-item" data-animation='from-left'  data-delay="0.5" data-animation-once
const animItems = document.querySelectorAll(".animate-item");
if (!isMobileDevice) {
	const elementInViewport = (element, offsetTop, offsetBottom, delay = 0) => {
		let bounds = element.getBoundingClientRect();
		if (element.dataset.delay) delay = element.dataset.delay;

		if ((bounds.top + bounds.height - bounds.height * offsetBottom > 0) && (window.innerHeight - bounds.top - bounds.height * offsetTop > 0)) {
			element.classList.add("animated");
			element.style.transitionDelay = `${delay}s`;
		} else if (!element.hasAttribute("data-animation-once")) {
			element.classList.remove("animated");
		}
	};
	if (animItems.length != 0) {
		animItems.forEach(el => {
			elementInViewport(el, .5, .5);
		})
		document.addEventListener('scroll', () => {
			animItems.forEach(el => {
				elementInViewport(el, .5, .5);
			});
		});
	};
} else {
	animItems.forEach((el) => {
		el.classList.add('animated')
	});
}
// -- //
;
	// // MODAL

// vars
const modalButtons = document.querySelectorAll('.modal-btn');
const lockPadding = document.querySelectorAll('.fixed-padding');
const modalCloseButtons = document.querySelectorAll('.close-modal');
const timeout = 800;
let currentCloseBtn;
let unlock = true;
let isMultipleClick = false;
let bodyWasNotLock;

// Проверки
if (typeof (disableScroll) !== 'function' && typeof (enableScroll) !== 'function') {
	console.error(`Тип данных переменных "disableScroll" и "enableScroll": "${typeof (disableScroll)} и ${typeof (enableScroll)}"! Для IOS НЕ будет выполнятся скрипт scrollLockIOS`);
}
// -- //
if (modalButtons.length > 0) {
	for (let index = 0; index < modalButtons.length; index++) {
		const modalCurrentButton = modalButtons[index];
		modalCurrentButton.addEventListener("click", function (e) {
			const modalName = modalCurrentButton.dataset.path;
			const curentModal = document.getElementById(modalName);
			if (curentModal != null) {
				modalOpen(curentModal);
				setTimeout(() => {
					modalCloseButtons.forEach((el) => {
						el.focus()
					})
				}, 100)
				e.preventDefault();
			} else {
				console.error(`Модальное окно не существует!\n modalName: ${modalName}, currentModal: ${curentModal}`);
			}
		});
	}
}

if (modalCloseButtons.length > 0) {
	for (let index = 0; index < modalCloseButtons.length; index++) {
		const el = modalCloseButtons[index];
		currentCloseBtn = el;
		el.addEventListener('click', function (e) {
			modalClose(el.closest('.modal'));
			e.preventDefault();
		});
	}
}
let previousActiveElement;
function modalOpen(curentModal) {
	if (curentModal && unlock) {
		previousActiveElement = document.activeElement;
		const modalActive = document.querySelector('.modal.open');
		if (modalActive) {
			modalClose(modalActive, false);
		}
		else {
			bodyLock();
		}
		curentModal.classList.add('open');

		curentModal.addEventListener("mousedown", function (e) {
			if (isMultipleClick === false) {
				if (!e.target.closest('.modal__content')) {
					modalClose(e.target.closest('.modal'));
				}
				isMultipleClick = true;
			} else {
				setTimeout(() => {
					isMultipleClick = false;
				}, timeout);
			}
		});
	}
}
function modalClose(modalActive, doUnlock = true) {
	if (unlock) {
		modalActive.classList.remove('open');
		if (doUnlock) {
			bodyUnlock();
		}
	}
	setTimeout(function () {
		previousActiveElement.focus();
	}, timeout);

}

function bodyLock() {
	const lockPaddingValue = window.innerWidth - body.offsetWidth + 'px';
	if (lockPadding.length > 0) {
		for (let index = 0; index < lockPadding.length; index++) {
			const el = lockPadding[index];
			el.style.paddingRight = lockPaddingValue;
		}
	}
	body.style.paddingRight = lockPaddingValue;
	if (!body.classList.contains('lock')) {
		bodyWasNotLock = true;
		if (isiPhone || isiPad || isiPod) {
			disableScroll();
		} else {
			body.classList.add('lock');
		}
	} else {
		bodyWasNotLock = false;
	}
	unlock = false;
	setTimeout(function () {
		unlock = true;
	}, timeout);
}

function bodyUnlock() {
	setTimeout(function () {
		if (lockPadding.length > 0) {
			for (let index = 0; index < lockPadding.length; index++) {
				const el = lockPadding[index];
				el.style.paddingRight = '0px';
			}
		}
		body.style.paddingRight = '0px';
		if (bodyWasNotLock == true) {
			if (isiPhone || isiPad || isiPod) {
				enableScroll();
			} else {
				body.classList.remove('lock');
			}
		}
	}, timeout);
}

document.addEventListener('keydown', function (e) {
	if (document.querySelector('.modal.open')) {
		if (e.which === 27) {
			const modalActive = document.querySelector('.modal.open');
			modalClose(modalActive);
		}
	}
});
(function () {
	if (!Element.prototype.closest) {
		Element.prototype.closest = function (css) {
			var node = this;
			while (node) {
				if (node.matches(css)) return node;
				else node = node.parentElement;
			}
			return null;
		};
	}
})();
(function () {
	if (!Element.prototype.matches) {
		Element.prototype.matches = Element.prototype.matchesSelector ||
			Element.prototype.webkitMatchesSelector ||
			Element.prototype.mozMatchesSelector ||
			Element.prototype.msMatchesSelector;
	}
})();;
	// -- //

	header.addEventListener('click', (e) => {
		switch (e.target) {
			case e.target.closest('.burger'):
				burger.toggle();
				break;

			case e.target.closest('.header__link'):
				e.preventDefault();
				if (media.matches) burger.close();
				setTimeout(() => {
					scrollToTarget(e.target.hash);
				}, 200);
				break;
		}
	})

	classificationSwiper.init();
	dietSwiper.init();

	window.addEventListener('resize', () => {
		resizeDebounce.init();
	});

	window.addEventListener('scroll', function () {
		scrollDebounce.init();
	});
})

